import React from 'react';
import Paragraph from '../paragraph/paragraph';

export type imageTitle = {
    title?: string;
    icone?: string;
}
type ImageTitleType = {
    modle: imageTitle[]
}
const ImageTitle = (props: ImageTitleType) => {
    return (
        <>
            {props.modle.map(item => {
                return (
                    <a href='#'>
                        <img className='image-title m-auto' src={item.icone} alt="" />
                        <Paragraph className='mt-2' textalign='text-center' color='color-paragraph' fonsize='text-base' title={item.title} />
                    </a>
                )
            })}
        </>
    );
};

export default ImageTitle;